/*-------------------BLOG---------------------------*/
.article {

	h1, h2, h3, h4, h5, h6 {
		line-height: 1.2;
		font-weight: normal !important;
	}

	&__text {
		h1, h2, h3, h4, h5, h6, img, blockquote{
			margin: 1rem 0;
		}
	}
}

.wp-block-image img{
	max-width: 100%;
	height: auto;
}

.nothing_found {
	padding: 89px 0 82px;
	width: 100%;
	
	&_block {
		color: #09ccb4;
		margin-bottom: 20px;
		font-size: 28.8px;
		line-height: 1.2;
		text-align: center;
		
		span {
			color: var(--red);
		}
	}

	&__back {
		padding: 5px 20px;
		text-transform: capitalize;
		text-shadow: none;
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: center;
		width: fit-content;
	}
}


section.blog {
	margin-bottom: 138px;
}

.blog {
	&__content {
		display: flex;
		align-items: stretch;
		justify-content: space-between;
		flex-wrap: wrap;
	}

	&__title {
		margin-bottom: 15px;
	}

	&__search {
		margin: 0 0 20px auto;
	}

	&__item {
		margin-bottom: 23px;
		padding-bottom: 15px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: calc(33.3333% - 20px);
		max-width: 364px;
		border-bottom: 1px solid #bbcac8;
	}

	&__img {
		margin-bottom: 16px;

		a {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			overflow: hidden;
		}

		img {
			width: 100%;
		}
	}

	&__date {
		margin-bottom: 16px;
		font-size: 14px;
		font-weight: 300;
		color: #bbcac8;
	}

	&__name {
		display: flex;
		align-items: center;
		margin-bottom: 22px;
		font-weight: 600;
		line-height: 1.1;
		font-size: 24px;
	}

	&__preview {
		margin-bottom: 20px;
	}

	&__more {
		display: flex;
		justify-content: flex-end;
		
		a {
			display: flex;
			align-items: center;

			svg {
				margin-left: 7px;
			}
			
			&:hover svg polygon {
				fill: #000;
			}
		}
	}
}

/*-------------------BLOG-ARTICLE---------------------------*/
section.article {
	margin-bottom: 160px;
}

.article {
	&__title {
		margin-bottom: 36px;
	}

	&__text {
    margin-bottom: 32px;
		
		.alignleft {
			margin: 16px 20px 16px 0;
		}

		p,
		ul {
			margin-right: auto;
			margin-left: auto;
			max-width: 93%;
		}
	}

	h3 {
		font-weight: 700;
	}

	&__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;

		&-title {
			font-size: 21px;
			margin-bottom: 15px;
		}
	}

	&__main  {
		padding: 0 15px;
		width: 78%;  
	}

	&__share-title {
    font-size: 21px;
    margin-bottom: 15px;
    text-align: center;
	}
}

.wp-block-image {
	aspect-ratio: 16/7.5;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 0 16px 0;
		
	img {
		width: 100%;
		height: auto;
	}
}

.sidebar {
	width: 20%;
	
	&__item {
		padding-bottom: 10px;
		border-bottom: 1px solid #27d2bd; 
	}
}

.related-article {
	margin-bottom: 25px;
	
	&__link {
		font-weight: 700;
		transition: .3s all;
		display: flex;
		line-height: 1.2;
	}

	&:hover {
		color: var(--red);
		text-decoration: underline;
	}
	
	&__item + &__item {
		margin-top: 7px;
	}

	&__img {
		overflow: hidden;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 8px;

		img {
			max-width: 100%;
		}
	}
}

.follow-us {
	margin-bottom: 20px;
}

.donate {
	padding-bottom: 15px;
	margin-bottom: 25px;
	
	&__way {
		div+div {
			margin-top: 10px;
		}

		div {
			font-weight: 700;
			font-size: 14px;
			display: flex;
			align-items: center;
			flex-wrap: wrap;
		}

		img {
			width: 35px;
			margin-right: 10px;
		}
	} 
	
	.article__content-title {
		display: flex;
		align-items: center;
		margin-bottom: 5px;
		
		img {
			margin-left: 10px;
			max-width: 45px;
		}
	}

	&__text {
		margin-bottom: 9px;
		font-size: 16px;
		line-height: 1.4;
	}
}

.youtube__item{
	margin-bottom: 20px;
}

.article {
	&__share {
		margin-bottom: 7px;
	} 

	&__share-links {
		display: flex;
		align-items: center;
		justify-content: center;
	}
}
/*shareon plugin style*/
.shareon {
	font-size: 0 !important;
	
	& > * {
		display: inline-block;
		position: relative;
		height: 24px;
		min-width: 16px;
		margin: 3px;
		padding: 6px 10px;
		background-color: #ccc;
		border-radius: 3.33333px;
		border: none;
		box-sizing: content-box;
		color: #fff;
		line-height: 1.5;
		transition: opacity .3s ease;
		vertical-align: middle;
	}
	
	& > :hover {
		border: none;
		cursor: pointer;
		opacity: .7;
	}
	
	& > :not(:empty) {
		font-size: 16px;
		text-decoration: none;

		&:before {
			position: relative;
			height: 100%;
			width: 28px;
			top: 0;
			left: 0;
			background-position: 0 50%;
		}
	}

	& > :before {
		display: inline-block;
		position: absolute;
		height: 20px;
		width: 20px;
		top: 8px;
		left: 8px;
		background-repeat: no-repeat;
		background-size: 20px 20px;
		content: "";
		vertical-align: bottom
	}
	
