@media (max-width:  1199px) {
	section.blog {
		margin-bottom: 0;
	}

	section.article {
		margin-bottom: 25px;
	}

	.nothing_found {
		padding: 89px 0 60px;
	}

	.article .video__btn svg {
		max-width: 50px;
	}
}

@media (max-width:  1024px) {
	.article .video__btn svg {
		max-width: 100%;
	}

	.sidebar,
	.article__main  {
		width: 100%;
	}

	.related-article {
		&__item {
			display: flex;
			align-items: flex-start;

			&+& {
				margin-top: 10px;
			}
		}

		&__img {
			flex-shrink: 0;
			width: 150px;
			margin-right: 10px;
		}
	}

	.follow-us {
		display: none;
	}

	.donate,
	section.article {
		margin-bottom: 60px;
	}

	.sidebar {
		order: 3;
		margin-top: 30px;
	}
}  

@media (max-width: 991px) {
	.blog {
		&__item {
			width: calc(50% - 15px);
		}

		&__name {
		font-size: 20px;
		}
	}
}

@media (max-width: 850px) {
	.blog {
		&__title {
			margin-bottom: 35px;
		}

		&__search {
			display: none;
		}
	}
	.nothing_found {
		padding: 80px 0 60px;
	}
}

@media (max-width: 767px) {
	.article {
		&__text {
			p, ul{
				max-width: 100%;
			}
		}

		&__title{
			text-align: center;
		}
	}

	.blog {
		&__item {
			width: 100%;
			max-width: 100%;
		}

		&__name {
			font-size: 18px;
		}
	}
}

@media (max-width:  600px) {
	.article {
		.wp-block-image {
			margin: 0 -30px 16px;
		}

		&__like {
			margin-bottom: 30px;

			iframe {
				width: 100%;
				height: 45px;
			}
		}
	}

	.blog__title {
		margin-bottom: 25px;
	}
	
	.nothing_found_block {
		font-size: 24px;
	}
}