/*-------------------BLOG---------------------------*/
.article h1, .article h2, .article h3, .article h4, .article h5, .article h6 {
  line-height: 1.2;
  font-weight: normal !important;
}

.article__text h1, .article__text h2, .article__text h3, .article__text h4, .article__text h5, .article__text h6, .article__text img, .article__text blockquote {
  margin: 1rem 0;
}

.wp-block-image img {
  max-width: 100%;
  height: auto;
}

.nothing_found {
  padding: 89px 0 82px;
  width: 100%;
}

.nothing_found_block {
  color: #09ccb4;
  margin-bottom: 20px;
  font-size: 28.8px;
  line-height: 1.2;
  text-align: center;
}

.nothing_found_block span {
  color: var(--red);
}

.nothing_found__back {
  padding: 5px 20px;
  text-transform: capitalize;
  text-shadow: none;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
}

section.blog {
  margin-bottom: 138px;
}

.blog__content {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-wrap: wrap;
}

.blog__title {
  margin-bottom: 15px;
}

.blog__search {
  margin: 0 0 20px auto;
}

.blog__item {
  margin-bottom: 23px;
  padding-bottom: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: calc(33.3333% - 20px);
  max-width: 364px;
  border-bottom: 1px solid #bbcac8;
}

.blog__img {
  margin-bottom: 16px;
}

.blog__img a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.blog__img img {
  width: 100%;
}

.blog__date {
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 300;
  color: #bbcac8;
}

.blog__name {
  display: flex;
  align-items: center;
  margin-bottom: 22px;
  font-weight: 600;
  line-height: 1.1;
  font-size: 24px;
}

.blog__preview {
  margin-bottom: 20px;
}

.blog__more {
  display: flex;
  justify-content: flex-end;
}

.blog__more a {
  display: flex;
  align-items: center;
}

.blog__more a svg {
  margin-left: 7px;
}

.blog__more a:hover svg polygon {
  fill: #000;
}

/*-------------------BLOG-ARTICLE---------------------------*/
section.article {
  margin-bottom: 160px;
}

.article__title {
  margin-bottom: 36px;
}

.article__text {
  margin-bottom: 32px;
}

.article__text .alignleft {
  margin: 16px 20px 16px 0;
}

.article__text p,
.article__text ul {
  margin-right: auto;
  margin-left: auto;
  max-width: 93%;
}

.article h3 {
  font-weight: 700;
}

.article__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.article__content-title {
  font-size: 21px;
  margin-bottom: 15px;
}

.article__main {
  padding: 0 15px;
  width: 78%;
}

.article__share-title {
  font-size: 21px;
  margin-bottom: 15px;
  text-align: center;
}

.wp-block-image {
  aspect-ratio: 16/7.5;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 16px 0;
}

.wp-block-image img {
  width: 100%;
  height: auto;
}

.sidebar {
  width: 20%;
}

.sidebar__item {
  padding-bottom: 10px;
  border-bottom: 1px solid #27d2bd;
}

.related-article {
  margin-bottom: 25px;
}

.related-article__link {
  font-weight: 700;
  transition: .3s all;
  display: flex;
  line-height: 1.2;
}

.related-article:hover {
  color: var(--red);
  text-decoration: underline;
}

.related-article__item + .related-article__item {
  margin-top: 7px;
}

.related-article__img {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
}

.related-article__img img {
  max-width: 100%;
}

.follow-us {
  margin-bottom: 20px;
}

.donate {
  padding-bottom: 15px;
  margin-bottom: 25px;
}

.donate__way div + div {
  margin-top: 10px;
}

.donate__way div {
  font-weight: 700;
  font-size: 14px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.donate__way img {
  width: 35px;
  margin-right: 10px;
}

.donate .article__content-title {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.donate .article__content-title img {
  margin-left: 10px;
  max-width: 45px;
}

.donate__text {
  margin-bottom: 9px;
  font-size: 16px;
  line-height: 1.4;
}

.youtube__item {
  margin-bottom: 20px;
}

.article__share {
  margin-bottom: 7px;
}

.article__share-links {
  display: flex;
  align-items: center;
  justify-content: center;
}

/*shareon plugin style*/
.shareon {
  font-size: 0 !important;
}

.shareon > * {
  display: inline-block;
  position: relative;
  height: 24px;
  min-width: 16px;
  margin: 3px;
  padding: 6px 10px;
  background-color: #ccc;
  border-radius: 3.33333px;
  border: none;
  box-sizing: content-box;
  color: #fff;
  line-height: 1.5;
  transition: opacity .3s ease;
  vertical-align: middle;
}

.shareon > :hover {
  border: none;
  cursor: pointer;
  opacity: .7;
}

.shareon > :not(:empty) {
  font-size: 16px;
  text-decoration: none;
}

.shareon > :not(:empty):before {
  position: relative;
  height: 100%;
  width: 28px;
  top: 0;
  left: 0;
  background-position: 0 50%;
}

.shareon > :before {
  display: inline-block;
  position: absolute;
  height: 20px;
  width: 20px;
  top: 8px;
  left: 8px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  content: "";
  vertical-align: bottom;
}

