@media (max-width: 1199px) {
  section.blog {
    margin-bottom: 0;
  }
  section.article {
    margin-bottom: 25px;
  }
  .nothing_found {
    padding: 89px 0 60px;
  }
  .article .video__btn svg {
    max-width: 50px;
  }
}

@media (max-width: 1024px) {
  .article .video__btn svg {
    max-width: 100%;
  }
  .sidebar,
  .article__main {
    width: 100%;
  }
  .related-article__item {
    display: flex;
    align-items: flex-start;
  }
  .related-article__item + .related-article__item {
    margin-top: 10px;
  }
  .related-article__img {
    flex-shrink: 0;
    width: 150px;
    margin-right: 10px;
  }
  .follow-us {
    display: none;
  }
  .donate,
  section.article {
    margin-bottom: 60px;
  }
  .sidebar {
    order: 3;
    margin-top: 30px;
  }
}

@media (max-width: 991px) {
  .blog__item {
    width: calc(50% - 15px);
  }
  .blog__name {
    font-size: 20px;
  }
}

@media (max-width: 850px) {
  .blog__title {
    margin-bottom: 35px;
  }
  .blog__search {
    display: none;
  }
  .nothing_found {
    padding: 80px 0 60px;
  }
}

@media (max-width: 767px) {
  .article__text p, .article__text ul {
    max-width: 100%;
  }
  .article__title {
    text-align: center;
  }
  .blog__item {
    width: 100%;
    max-width: 100%;
  }
  .blog__name {
    font-size: 18px;
  }
}

@media (max-width: 600px) {
  .article .wp-block-image {
    margin: 0 -30px 16px;
  }
  .article__like {
    margin-bottom: 30px;
  }
  .article__like iframe {
    width: 100%;
    height: 45px;
  }
  .blog__title {
    margin-bottom: 25px;
  }
  .nothing_found_block {
    font-size: 24px;
  }
}
